@import url(https://fonts.googleapis.com/css?family=Sacramento&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scrollbar-width: thin;
  scrollbar-color: #ffa500 rgb(58, 56, 56);
}

/* Works on Chrome/Edge/Safari */
*::-webkit-scrollbar {
  width: 12px;
}
*::-webkit-scrollbar-track {
  background:rgb(58, 56, 56);
}
*::-webkit-scrollbar-thumb {
  background-color: #ffa500;
  border-radius: 20px;
  border: 3px solid rgb(58, 56, 56);
}

body {
  padding: 2vw; 
  background: #222;
  background-image: repeating-linear-gradient(
    to bottom,
    transparent 7px,
    rgba(0, 0, 0, 0.8) 9px,
    rgba(0, 0, 0, 0.8) 13px,
    transparent 13px
  );
}

.header-container{
  max-width: 768px;
  margin: 0 auto 2.5rem auto;
}

.main-container{
  max-width: 1140px;
  margin: 0 auto;
}

h2 {
  color: white;
  text-align: center;
  margin: 1.8rem 0;
  font-size: 1.8rem;
}

@media only screen and (max-width: 768px) {
  h2 {
    font-size: 1.3rem;
  }
}

h4{
  color: white;
  text-align: center;
  margin: 1.7rem 0;
}

.center {
  display: flex;
  justify-content: center;
}

.btn {
  background-color: black;
  color: orange;
  cursor: pointer;
  padding: 15px 32px;
  border: 2px solid #ffa500;
  text-align: 0 auto;
  border-radius: 5px;
  font-size: 1em;
  transition-duration: .4s;
  margin: 1.5rem auto;
  font-weight: 400;
  letter-spacing: 2px;
}

.btn:hover {
  background-color: #ffa500;
  color: #fff;
}

.add-btn{
  background: transparent;
  border: none;
  padding-left: .5rem;
}


input {
  width: 70%;
  padding: 12px 20px;
  border: 2px solid #ffa500;
  font-size: medium;
}

.add-movie{
  background: #ffa500;
  color: white;
  border: none;
  padding: 12px 20px;
  cursor: pointer;
  font-size: 1rem;
}

.badges{
  display: grid;
  grid-template-columns : 1fr 1fr 1fr;
  width: 77%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1rem;
  grid-gap: 10px 10px;
  gap: 10px 10px;
}


.badge{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.4em 0.4em;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  vertical-align: baseline;
  color: white;
  background-color:#ffa500;border-radius: .3rem;
  letter-spacing: 1px;
}







