.lds-default {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .lds-default div {
    position: absolute;
    width: 6px;
    height: 6px;
    background: #ffa500;
    border-radius: 50%;
    -webkit-animation: lds-default 1.2s linear infinite;
            animation: lds-default 1.2s linear infinite;
  }
  .lds-default div:nth-child(1) {
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    top: 37px;
    left: 66px;
  }
  .lds-default div:nth-child(2) {
    -webkit-animation-delay: -0.1s;
            animation-delay: -0.1s;
    top: 22px;
    left: 62px;
  }
  .lds-default div:nth-child(3) {
    -webkit-animation-delay: -0.2s;
            animation-delay: -0.2s;
    top: 11px;
    left: 52px;
  }
  .lds-default div:nth-child(4) {
    -webkit-animation-delay: -0.3s;
            animation-delay: -0.3s;
    top: 7px;
    left: 37px;
  }
  .lds-default div:nth-child(5) {
    -webkit-animation-delay: -0.4s;
            animation-delay: -0.4s;
    top: 11px;
    left: 22px;
  }
  .lds-default div:nth-child(6) {
    -webkit-animation-delay: -0.5s;
            animation-delay: -0.5s;
    top: 22px;
    left: 11px;
  }
  .lds-default div:nth-child(7) {
    -webkit-animation-delay: -0.6s;
            animation-delay: -0.6s;
    top: 37px;
    left: 7px;
  }
  .lds-default div:nth-child(8) {
    -webkit-animation-delay: -0.7s;
            animation-delay: -0.7s;
    top: 52px;
    left: 11px;
  }
  .lds-default div:nth-child(9) {
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
    top: 62px;
    left: 22px;
  }
  .lds-default div:nth-child(10) {
    -webkit-animation-delay: -0.9s;
            animation-delay: -0.9s;
    top: 66px;
    left: 37px;
  }
  .lds-default div:nth-child(11) {
    -webkit-animation-delay: -1s;
            animation-delay: -1s;
    top: 62px;
    left: 52px;
  }
  .lds-default div:nth-child(12) {
    -webkit-animation-delay: -1.1s;
            animation-delay: -1.1s;
    top: 52px;
    left: 62px;
  }
  @-webkit-keyframes lds-default {
    0%, 20%, 80%, 100% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.5);
    }
  }
  @keyframes lds-default {
    0%, 20%, 80%, 100% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.5);
    }
  }
  
.cards{
    display: grid;
    grid-template-columns : 1fr 1fr 1fr;
    grid-gap: 25px 25px;
    gap: 25px 25px;
    text-align: center;
    color: #fff;
    margin-top: 5rem;
    
  }
  
  @media only screen and (max-width: 600px) {
    .cards {
      grid-template-columns : 1fr;
    }
  } 
  
  @media only screen and (min-width: 600px) {
    .cards {
      grid-template-columns : 1fr 1fr;
    }
  } 
  
  @media only screen and (min-width: 992px) {
    .cards {
      grid-template-columns : 1fr 1fr 1fr;
    }
  } 
  
  .card{
    max-width: 25rem;
   display: flex;
   flex-direction: column;
   min-width: 0;
   background-clip: border-box;
   border: 2px solid ;
   padding: 10px;
   box-shadow: 1px 1px 1px ;
   border-radius: .25rem;
   text-align: center;
   animation: card-blink 12s infinite;
   -webkit-animation: card-blink 12s infinite;
  }
  
  @-webkit-keyframes card-blink {
    20%,
    24%,
    55% {
      border-color: #ffa500;
      box-shadow: none;
    }
  
    0%,
    19%,
    21%,
    23%,
    25%,
    54%,
    56%,
    100% {
      border-color: #ecb958;
      box-shadow: 0 0 1px #ffa500, 0 0 5px #ffa500, 0 0 8px #ffa500,
      0 0 16px #ffa500,0 0 20px #ff0000, 0 0 1px #ff8d00;
    }
  }
  
  @keyframes card-blink {
    20%,
    24%,
    55% {
      border-color: #ffa500;
      box-shadow: none;
    }
  
    0%,
    19%,
    21%,
    23%,
    25%,
    54%,
    56%,
    100% {
      border-color: #ecb958;
      box-shadow: 0 0 1px #ffa500, 0 0 5px #ffa500, 0 0 8px #ffa500,
      0 0 16px #ffa500,0 0 20px #ff0000, 0 0 1px #ff8d00;
    }
  }
  
  
  .card-body{
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    text-align: left;
    color: white;
    text-align: center;
    padding: 1rem;
    margin-bottom: 1.5rem;
  }
  
  .card a {
    margin-top: auto;
    text-decoration: none;
    font-weight: 600;
  }
  
  .card-title {
    font-size: 1.3rem;
  }
  
.scrollTop {
    position: fixed; 
    left: calc(100% - 7rem);
    bottom: 20px;
    font-size: 4rem;
    color: #df9100;
    align-items: flex-end;
    z-index: 1000;
    cursor: pointer;
    -webkit-animation: fadeIn 0.3s;
            animation: fadeIn 0.3s;
    transition: opacity 0.4s;
    opacity: 0.5;
    margin-right: 30rem;
  }
  
  .scrollTop:hover{
    opacity: 1;
  }
  
  @-webkit-keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }
  
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }
  
  
  
  
  
  
.alert {
    width: 65%;
    margin: 20px auto;
    padding-top: 20px;
    display: flex;
    justify-content: space-between;
    padding: 15px;
    padding-right: 15px;
    border: 1px solid transparent;
    border-top-color: transparent;
    border-right-color: transparent;
    border-bottom-color: transparent;
    border-left-color: transparent;
    border-radius: 4px;
    text-align: left;
    font-weight: 600;
  }
  
  @media only screen and (max-width: 768px) {
    .alert {
      width: 75%;
    }
  }
  
  i.close {
    text-decoration: none !important;
    font-size: 18px !important;
    line-height: 1.2;
    text-shadow: 0 1px 0 #fff;
    font-weight: 700;
    opacity: 0.5;
    color:  #fff;
    cursor: pointer;
  }
  
  i.close:hover {
    opacity: 1;
    transition: 0.5s;
  }
  
  .alert-danger {
    color: #fff;
    background-color:#f5af2e;
    border-color: #df9100;
  }
