.scrollTop {
    position: fixed; 
    left: calc(100% - 7rem);
    bottom: 20px;
    font-size: 4rem;
    color: #df9100;
    align-items: flex-end;
    z-index: 1000;
    cursor: pointer;
    animation: fadeIn 0.3s;
    transition: opacity 0.4s;
    opacity: 0.5;
    margin-right: 30rem;
  }
  
  .scrollTop:hover{
    opacity: 1;
  }
  
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }
  
  
  
  
  
  