.cards{
    display: grid;
    grid-template-columns : 1fr 1fr 1fr;
    gap: 25px 25px;
    text-align: center;
    color: #fff;
    margin-top: 5rem;
    
  }
  
  @media only screen and (max-width: 600px) {
    .cards {
      grid-template-columns : 1fr;
    }
  } 
  
  @media only screen and (min-width: 600px) {
    .cards {
      grid-template-columns : 1fr 1fr;
    }
  } 
  
  @media only screen and (min-width: 992px) {
    .cards {
      grid-template-columns : 1fr 1fr 1fr;
    }
  } 
  
  .card{
    max-width: 25rem;
   display: flex;
   flex-direction: column;
   min-width: 0;
   background-clip: border-box;
   border: 2px solid ;
   padding: 10px;
   box-shadow: 1px 1px 1px ;
   border-radius: .25rem;
   text-align: center;
   animation: card-blink 12s infinite;
   -webkit-animation: card-blink 12s infinite;
  }
  
  @-webkit-keyframes card-blink {
    20%,
    24%,
    55% {
      border-color: #ffa500;
      box-shadow: none;
    }
  
    0%,
    19%,
    21%,
    23%,
    25%,
    54%,
    56%,
    100% {
      border-color: #ecb958;
      box-shadow: 0 0 1px #ffa500, 0 0 5px #ffa500, 0 0 8px #ffa500,
      0 0 16px #ffa500,0 0 20px #ff0000, 0 0 1px #ff8d00;
    }
  }
  
  @keyframes card-blink {
    20%,
    24%,
    55% {
      border-color: #ffa500;
      box-shadow: none;
    }
  
    0%,
    19%,
    21%,
    23%,
    25%,
    54%,
    56%,
    100% {
      border-color: #ecb958;
      box-shadow: 0 0 1px #ffa500, 0 0 5px #ffa500, 0 0 8px #ffa500,
      0 0 16px #ffa500,0 0 20px #ff0000, 0 0 1px #ff8d00;
    }
  }
  
  
  .card-body{
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    text-align: left;
    color: white;
    text-align: center;
    padding: 1rem;
    margin-bottom: 1.5rem;
  }
  
  .card a {
    margin-top: auto;
    text-decoration: none;
    font-weight: 600;
  }
  
  .card-title {
    font-size: 1.3rem;
  }
  